@import "colors.scss";
@import "media.scss";
#main-menu{    
    .nav-menu{
        padding-left: 0;
        margin:0;
        background: $nav-menu-back-color;
        .nav-menu-item{
            position: relative;
            display: inline-block;
            list-style-type: none;
            padding: 19px 0 16px 0;
            margin: 0!important;
            background-color: $nav-menu-back-color;
            a{
                border: transparent 1px solid;
                border-top: transparent 3px solid;
                border-bottom: none;   
                padding: 13px 25px;
                margin:0;
                color: $nav-text-color;
                text-decoration: none;                
            }
            a:hover{
                border-top: $blue-nav-element 3px solid
            }            
        }
        .nav-menu-item.active{
            z-index: 1000;
            a{           
                border: $nav-element-border-color 1px solid;
                border-top: $blue-nav-element 3px solid;                
                border-bottom: none;                                
            }
        }
    }
   
}
@media (min-width: $screen-sm-min) {
     .nav-submenu-container{
        position: absolute;        
        height: auto;        
        left:0;
        top: 49px;
        width:100%;
        background: $nav-menu-back-color;
        border: solid 1px #ddd;        
        padding: 15px 30px 10px 30px;
        box-shadow: 2px 2px 2px rgba(0,0,0,0.2);
        ul{            
            float: left;
            padding: 0 30px 0 30px;                   
            border-right: #ddd 1px solid;   
            margin-bottom: 0;
            li{
                list-style-type: none;
                padding: 5px 0;
                a{color: $nav-text-color}                                        
            }
          }
        ul:last-child{
            padding: 0 0 0 40px;    
            border-right: none;                    
         }
        ul:first-child{
            padding: 0 40px 0 0;                        
        }
       
    }
}
@media (min-width: $screen-md-min) {
    .nav-submenu-container{       
        padding: 15px 40px 10px 40px;       
       
    }

}